body {
  position: relative;
  min-width: 375px;
  margin: 0;
  background: #fff;
  font-size: 14px;
  line-height: 1.5;
  text-size-adjust: none;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

*,
::before,
::after {
  box-sizing: border-box;
}

.blind {
  position: absolute;
  width: 1px;
  height: 1px;
  font-size: 1px;
  overflow: hidden;
  opacity: 0;
}

.ko-KR {
  font-family: 'Noto Sans KR', 'Apple SD Gothic Neo', NanumBarunGothic, NanumGothic, 'Malgun Gothic', dotum, sans-serif;
  word-break: keep-all;
}

img {
  max-width: 100%;
  height: auto;
}

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

.header {
  position: absolute;
  top: 30px;
  left: 30px;
  right: 30px;
  z-index: 1;
}

.header .nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header .nav__logo {
  width: 50px;
  height: 50px;
  margin: 0;
  padding: 0;
}

.header .nav__logo img {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  vertical-align: top;
}

.header .nav__link--item {
  margin-left: 32px;
  font-size: 20px;
  color: #1e232d;
  line-height: 20px;
}

.header .nav__link--item:first-child {
  margin-left: 0;
}

.header .nav__link--item::after {
  content: '';
  display: inline-block;
  width: 13px;
  height: 13px;
  margin: 5px 0 0 5px;
  background: url(/src/img/sp_newpf.png) no-repeat 0 0/150px 150px;
  vertical-align: top;
}

.section {
  position: relative;
  width: 100%;
  box-sizing: border-box;
}

.section--fz {
  height: 100vh;
  padding: 30px 30px 35px 30px;
  background: #f6f6f6;
}

.section__down {
  position: absolute;
  bottom: 35px;
  left: 50%;
  width: 42px;
  height: 23px;
  padding: 0;
  background: transparent url(/src/img/sp_newpf.png) no-repeat -18px 0/150px 150px;
  border: 0;
  appearance: none;
  transform: translateX(-50%);
  z-index: 1;
}

.section__title {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 0;
  padding: 0;
  font-family: SF Pro KR,SF Pro Display,SF Pro Icons,Apple Gothic,HY Gulim,MalgunGothic,HY Dotum,Lexi Gulim,Helvetica Neue,Helvetica,Arial,sans-serif;
  font-size: 80px;
  transform: translate(-50%, -50%);
  animation: opacity 5s;
}

.section__myinfo {
  height: auto;
  padding: 136px 100px 50px;
  background: #fff;
  text-align: center;
}

.section__myinfo--text {
  font-family: SF Pro KR,SF Pro Display,SF Pro Icons,Apple Gothic,HY Gulim,MalgunGothic,HY Dotum,Lexi Gulim,Helvetica Neue,Helvetica,Arial,sans-serif;
  font-size: 24px;
  color: #1e232d;
  line-height: 46px;
}

@keyframes opacity {
  0% {
    opacity: 0.25;
  }
  100% {
    opacity: 1;
  }
}
